@import "null";
@import "fonts";
@import "variables";
@import "root";


html, body {
  font-family: Raleway, serif;
  background-color: $b-color;
  position: relative;
  margin: 0 auto;
  width: 1200px;
}

@mixin sizes($h:none, $w:none) {
  height: $h; width: $w;
}

@mixin flex-settings($j-content:center, $a-items:none,$f-direction:row) {
  display: flex;
  justify-content: $j-content;
  align-items: $a-items;
  flex-direction: $f-direction;
}
@mixin button($w:279px) {
  height: 50px;
  background: linear-gradient(94.26deg, #C89AFC 9.51%, #7C6AFA 90.23%);
  border: 1px solid #000000;
  font-weight: bold;
  border-radius: 50px;
  width: $w;
}
