@import "src/styles/index";
.header {
  border: $markup;
  @include flex-settings($j-content:end);
  @include sizes($h:112px, $w:1200px);
  position:fixed;
  .header_logo {
    position: absolute;
    top:30px; left: 120px;
  }
  .navbar {
    border: $markup;
    @include flex-settings($a-items:center, $j-content:space-around);
    @include sizes($h:100%, $w:490px);
    margin-right: 110px;
    .navbarItem {
      display: inline-block;
    }
  }
}