input,
button,
textarea,
select,
a {
  font: inherit;
  color: inherit;
  text-decoration: none;
  font-weight: bold;
}
body,
h1,
h2,
h3,
h4,
p,

li,
figure,
figcaption,
blockquote,
dl,
dd {
  margin: 0;
  padding: 0;
}
button {
  background-color: rgba(255, 255, 255, 0);
  border: none;
}